import { Component, OnInit, Input, 	Output, EventEmitter } from '@angular/core';
declare const $: any;
@Component({
  selector: 'app-achievementsnippet',
  templateUrl: './achievementsnippet.component.html',
  styleUrls: ['./achievementsnippet.component.css']
})
export class AchievementsnippetComponent implements OnInit {
	@Input() subject;
	@Input() date;
	@Input() description;
	@Input() comment;
	@Output() deleteSnippetEvent: EventEmitter<any> = new EventEmitter<any>();       
	//handle data/event pass to parent component
  	constructor() { }
	
	deleteSnippet(event){
		var element = $(event.target).parents("app-achievementsnippet");
		var number = element.prevAll("app-achievementsnippet").length;		//how many snippet before current one
		this.deleteSnippetEvent.emit(number);
	}
  	ngOnInit() {
		
  	}

}



