import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
/* --------	3rd party module --------  */
import { ScrollbarModule } from 'ngx-scrollbar';

import { AppComponent } from './app.component';
import { UseraccountComponent } from './useraccount/useraccount.component';
import { AppRoutingModule } from './/app-routing.module';
import { UserachievementComponent } from './useraccount/userachievement/userachievement.component';
import { AchievementsnippetComponent } from './useraccount/userachievement/achievementsnippet/achievementsnippet.component';
import { UserhistoryComponent } from './useraccount/userhistory/userhistory.component';
import { AchievementdataService } from './services/globalvariables/achievementdata.service';
import { UsercollectionComponent } from './useraccount/usercollection/usercollection.component';
import { UsersettingComponent } from './useraccount/usersetting/usersetting.component';
import { UsersettingService } from './services/globalvariables/usersetting.service';
import { MessageboxComponent } from './messagebox/messagebox.component';

@NgModule({
  declarations: [
	  AppComponent,
	  UseraccountComponent,
	  UserachievementComponent,
	  AchievementsnippetComponent,
	  UserhistoryComponent,
	  UsercollectionComponent,
	  UsersettingComponent,
	  MessageboxComponent
  ],
  imports: [
	  BrowserModule,
	  AppRoutingModule,
	  FormsModule,								//FormModule is required if we want to use ngModel
	  ScrollbarModule							//3rd party module for custom scrollbar in Angular
  ],
  providers: [  
  	AchievementdataService, UsersettingService
  ],
  /*
  	All components about to be loaded "dynamically" need to be declared in the entryComponents array of 
	app module definition. And of course, it needs be declared in declarations array as well.
  */
  entryComponents: [AchievementsnippetComponent],
  bootstrap: [AppComponent]
})
export class AppModule { }



