import {
	Component, OnInit,
	ViewChild, ViewContainerRef, ComponentFactoryResolver, ComponentRef, ComponentFactory
} from '@angular/core';

import {AchievementsnippetComponent} from './achievementsnippet/achievementsnippet.component';
import {AchievementdataService} from '../../services/globalvariables/achievementdata.service';
declare const $: any;

@Component({
	selector: 'app-userachievement',
  	templateUrl: './userachievement.component.html',
  	styleUrls: ['./userachievement.component.css']
})
export class UserachievementComponent implements OnInit {
	
	public snippetDeleteIndex;
	/*
		In template we have <template #snippetContainer></template>, the reference #snippetContainer
		refers to this element, which is our entry point (where to dynamically add component)
	*/
	@ViewChild('snippetContainer', { read: ViewContainerRef }) entry: ViewContainerRef;
	/* 
		Get the element, read "as" ViewContainerRef, therefore, entry (which refers to the DOM element <template>)
		is a variable whose type is ViewContainerRef.
		The reason why we need ViewContainerRef is that it exposes many useful APIs to manage a set of components
		(1) createComponent(factory), where factory depens on the component protoype passed to 
			 ComponentFactoryResolver.
			 Usually the "create" will append a "view" (refers to the new component DOM template)
		(2) length():	It implies one container can contains multiple elements, here, ViewContainerRef supports
			 containing all dynamically created components' views
		(3) clear():	destory all elements inside
		(4) remove(index)	destory specific one
		More useful APIs:
		https://angular.io/api/core/ViewContainerRef
	*/
	private data;
  	constructor(private resolver: ComponentFactoryResolver, private achievementdata: AchievementdataService) {
		this.data=achievementdata.data;			// gain global stored data from custom service
	}

  	ngOnInit() {
	  	$("app-userachievement").css("flex","1");
		for(var i=0;i<this.data.length;i++)
			this.createSnippet(this.data[i])
  	}
	configDropdown(event){
		event.stopPropagation();
		$("#useraccountAchievementMain div.configDropdown").slideToggle("slow");
	}
	createSnippet(data){
		//put prototype into ComponentFactory, before that, make sure add the component into entryComponents array of AppModule
		var snippetFactory = this.resolver.resolveComponentFactory(AchievementsnippetComponent);
		var snippetRef = this.entry.createComponent(snippetFactory);
		$(snippetRef.location.nativeElement).css("width","50%");			
		// each row can have two snippet
		snippetRef.instance.subject = data!=undefined?data.subject:"";
		snippetRef.instance.date = data!=undefined?data.date:"";
		snippetRef.instance.description = data!=undefined?data.description:"";
		snippetRef.instance.comment = data!=undefined?data.comment:"";
		//subscribe the raised custom event 'deleteSnippetEvent' from child component by using this.deleteSnippet as handler
		snippetRef.instance.deleteSnippetEvent.subscribe((number)=>{this.deleteSnippet(number)});
		if(data===undefined)
			snippetRef.location.nativeElement.scrollIntoView({behavior: "smooth"});
	}
	deleteSnippet(number){
		this.entry.remove(number);
	}
	removeSnippet(){
		this.entry.clear();
		$("#achievementConfirmScreen2").css("display","flex");
		$("#achievementConfirmScreen2 div.confirmTextDelete").animate(
   			{opacity: '1'} , 1500, "linear", function(){
				$(this).animate({opacity: '0'},1500,"linear");
				$("#achievementConfirmScreen2").fadeOut(1500);
			}
		);
	}
	saveSnippet(){
		var data=[];
		$("#achievementSnippetContainer>app-achievementsnippet").each(function(){
			var object ={
				subject: $(this).find("input").eq(0).val(),
				date: $(this).find("input").eq(1).val(),
				description: $(this).find("textarea").val()
			}													  
			data.push(object);
		});
		this.achievementdata.data = data;
		$("#achievementConfirmScreen1").css("display","flex");
		$("#achievementConfirmScreen1 div.confirmTextSave").animate(
   			{opacity: '1'} , 1500, "linear", function(){
				$(this).animate({opacity: '0'},1500,"linear");
				$("#achievementConfirmScreen1").fadeOut(1500);
			}
		);
	}
}



