import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import {ScrollbarComponent} from 'ngx-scrollbar';
declare const $: any;

@Component({
  selector: 'app-messagebox',
  templateUrl: './messagebox.component.html',
  styleUrls: ['./messagebox.component.css']
})
export class MessageboxComponent implements OnInit, AfterViewInit {
	chatType:number=1;
	friendList=[
		{ID: 1, name: "Yaopeng Liu", profile: "assets/img/avatar/Avatar_01.png", online: true, level: 15, province: "ON"},
		{ID: 2, name: "Alice Eden", profile: "assets/img/avatar/Avatar_02.png", online: false, level: 12, province: "AB"},
		{ID: 3, name: "Bruce Lee", profile: "assets/img/avatar/Avatar_03.png", online: true, level: 13, province: "QC"},
		{ID: 4, name: "James Cocordan", profile: "assets/img/avatar/Avatar_04.png", online: false, level: 9, province: "NT"},
		{ID: 5, name: "Kanzaki Moe", profile: "assets/img/avatar/Avatar_05.png", online: true, level: 25, province: "ON"},
		{ID: 6, name: "Jack William", profile: "assets/img/avatar/Avatar_06.png", online: true, level: 11, province: "ON"},
		{ID: 7, name: "Paul Ryan", profile: "assets/img/avatar/Avatar_07.png", online: false, level: 12, province: "BC"}
	];
	selectedFriend:number;
  	constructor() {
	}
	
	@ViewChild(ScrollbarComponent) scrollRef: ScrollbarComponent;
	
  	ngOnInit() {
		$("#chatBarForMessage div.chatList>div.chatGroupBlock>button").eq(this.chatType-1).toggleClass("actived");
  		this.scrollRef.trackX=false;
		this.scrollRef.trackY=true;
		var bar = this.scrollRef.barYRef.nativeElement;
		var thumb = this.scrollRef.thumbYRef.nativeElement;
		$(bar).css({
			"width": "0.5rem", "border-radius": "0.5rem", "background": "rgba(237, 204, 204, 0.88)"
		});
		$(thumb).css({
			"border-radius": "0.5rem", "background": "rgba(35, 137, 255, 0.74)",
			"background-image": "linear-gradient(45deg,rgba(255,255,255,.15) 25%,transparent 25%,transparent 50%,rgba(255,255,255,.15) 50%,rgba(255,255,255,.15) 75%,transparent 75%,transparent)",
			"background-size": "1rem 1rem", "animation": "progress-bar-stripes 0.5s linear infinite"		
		});
	}
	
	ngAfterViewInit(){	
		this.scrollRef.scrollYTo(1, 50);
		this.scrollRef.scrollYTo(0, 50);
	}
	
	chatTypeChange(event){
		var type= $(event.target).attr("chattype");
		if(type===undefined)
			type=$(event.target).parent("button").attr("chattype");
		$("#chatBarForMessage div.chatList>div.chatGroupBlock>button").eq(this.chatType-1).toggleClass("actived");
  		this.chatType = Number(type);
		$("#chatBarForMessage div.chatList>div.chatGroupBlock>button").eq(this.chatType-1).toggleClass("actived");
	}
	
	friendSelection(event){
		var id = $(event.target).attr("friendID");
		if(id===undefined)
			id=$(event.target).parents("div[friendID]").attr("friendID");
		if(this.selectedFriend) 
			$("#chatBarForMessage div.currentChatList>div.shortcutForChatter").eq(this.selectedFriend-1).toggleClass("active");
		this.selectedFriend = Number(id);
		$("#chatBarForMessage div.currentChatList>div.shortcutForChatter").eq(this.selectedFriend-1).toggleClass("active");
		
	}

}



