import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { UseraccountComponent } from './useraccount/useraccount.component';
import { UserachievementComponent } from './useraccount/userachievement/userachievement.component';
import { UserhistoryComponent } from './useraccount/userhistory/userhistory.component';
import { UsercollectionComponent } from './useraccount/usercollection/usercollection.component';
import { UsersettingComponent } from './useraccount/usersetting/usersetting.component';
import { MessageboxComponent } from './messagebox/messagebox.component';

const routes: Routes = [
  	{ path: '', redirectTo: '/useraccount/achievement', pathMatch: 'full' },
  	{ path: 'useraccount', component: UseraccountComponent,
	  children:[
		  { path: '', redirectTo: 'achievement', pathMatch: 'full' },
		  { path: 'achievement', component: UserachievementComponent },
		  { path: 'history', component: UserhistoryComponent },
		  { path: 'collection', component: UsercollectionComponent },
		  { path: 'setting', component: UsersettingComponent }
	  ]
	},
	{ path: 'messagebox', component: MessageboxComponent }
];

@NgModule({
	imports: [ RouterModule.forRoot(routes) ],
  	exports: [ RouterModule ]
})
export class AppRoutingModule { }



