import { Component, OnInit, OnDestroy } from '@angular/core';
declare const $: any;

@Component({
  selector: 'app-useraccount',
  templateUrl: './useraccount.component.html',
  styleUrls: ['./useraccount.component.css']
})
export class UseraccountComponent implements OnInit, OnDestroy {
  	private titleTimer;
  	private titleDirection:boolean = false;
	private currentRouter= 'achievement'							// by default
  	constructor() { }

  	ngOnInit() {
	  	var array = ['70%', '60%', '45%', '90%'];
	  	$("div.infoblock div.progress-bar").each((index, element)=>{
		  	$(element).css("width",array[index]);
	  	});
	  	$("a[routerLink='"+this.currentRouter+"']").addClass("focused");
  	}
	ngOnDestroy() {
	  	clearInterval(this.titleTimer);
  	}
	
	clickRouter(event){
		event.preventDefault();
		var element =$(event.target);
		$("a[routerLink='"+this.currentRouter+"']").removeClass("focused");
		this.currentRouter = element.attr("routerLink");
		element.addClass("focused");
	}

}



